<template>
	<div>
		  <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item><i class="el-icon-date"></i> 交易</el-breadcrumb-item>
                <el-breadcrumb-item>交易查证</el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="form-box">
            <el-form ref="form" :model="form" label-width="140px">
                 <el-form-item label="原交易日期">
                    <el-col :span="11">
                        <el-date-picker type="date" placeholder="选择日期" v-model="form.date1" style="width: 100%;"></el-date-picker>
                    </el-col>                   
                </el-form-item>
                <el-form-item label="交易类型">
                    <el-select v-model="form.region" placeholder="提现">
                       <el-option key="bbk" label="充值" value="bbk"></el-option><el-option key="bbk" label="提现" value="bbk"></el-option><el-option key="bbk" label="其它" value="bbk"></el-option>  
                    </el-select>
                </el-form-item>               
                <el-form-item>
                    <el-button type="primary" @click="onSubmit">查询</el-button>
                </el-form-item>
            </el-form>
        </div>
	</div>
</template>
<script>

    export default {
        data: function(){
            return {
                form: {
                    name: '',
                    region: '',
                    date1: '',
                    date2: '',
                    delivery: true,
                    type: ['步步高'],
                    resource: '小天才',
                    desc: ''
                }
            }
        },

        methods: {
            onSubmit() {
                this.$message.success('提交成功！');
            }
        }
    }
</script>
<style>

</style>


