<template>
    <div>
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item><i class="el-icon-date"></i> 交易</el-breadcrumb-item>
                <el-breadcrumb-item>充值交易</el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="form-box">
            <el-form ref="form" :model="form" label-width="100px">
                <el-form-item label="账户号">
                    <el-input v-model="form.name"></el-input>
                </el-form-item>
                 <el-form-item label="手机号">
                    <el-input v-model="form.name"></el-input>
                </el-form-item>

                <el-form-item label="充值类型">
                    <el-select v-model="form.region" placeholder="银行卡">
                        <el-option key="bbk" label="银行卡" value="bbk"></el-option>
                        <el-option key="xtc" label="恒丰对公账户" value="xtc"></el-option>
                    </el-select>
                </el-form-item>
                 <el-form-item label="付款账号">
                    <el-input v-model="form.name"></el-input>
                </el-form-item>
                 <el-form-item label="交易金额">
                    <el-input v-model="form.name"></el-input>
                </el-form-item>
                 <el-form-item label="付款人姓名">
                    <el-input v-model="form.name"></el-input>
                </el-form-item>
                <el-form-item label="清算方式">
                    <el-select v-model="form.region" placeholder="T+0准实时">
                        <el-option key="bbk" label="T+0准实时" value="bbk"></el-option>
                        <el-option key="xtc" label="T+1准实时" value="xtc"></el-option>
                    </el-select>
                </el-form-item>               
                <el-form-item>
                    <el-button type="primary" @click="onSubmit">确认</el-button>
                    <el-button>重置</el-button>
                </el-form-item>
            </el-form>
        </div>

    </div>
</template>

<script>

    export default {
        data: function(){
            return {
                form: {
                    name: '',
                    region: '',
                    date1: '',
                    date2: '',
                    delivery: true,
                    type: ['步步高'],
                    resource: '小天才',
                    desc: ''
                }
            }
        },

        methods: {
            onSubmit() {
                this.$message.success('提交成功！');
            }
        }
    }
</script>
<style>
	.crumbs{
    margin-bottom: 20px;
}
.form-box{
    width:600px;
    margin-left: 162px;
}
.form-box .line{
    text-align: center;
}
.el-time-panel__content::after, .el-time-panel__content::before {
    margin-top: -7px;
}
</style>


