import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/common/Home'
import Login from '../components/page/Login'
import Pretrad from '../components/page/Pretrad.vue'
import Cashdeal from '../components/page/Cashdeal.vue'
import Tranverificat from '../components/page/Tranverificat.vue'


Vue.use(Router)

export default new Router({
	 mode:'history',
  // routes: [ 	
  //   {
  //     path: '/login',
  //     name: 'Login',
  //     component: Login
  //   }
  // ]
  routes: [ 

    {
      path: '/',
      name: 'Login',
      component: Login
    },
    {
      path: '/home',
      name: 'Home',
      component: Home,
      children:[
     	{path:'/pretrad',component:Pretrad},
		{path:'/cashdeal',component:Cashdeal},
		{path:'/tranverificat',component:Tranverificat},
		{path:'/*',component:Login}
		
      ]
    }
  ]
})



